import React, { Component } from "react";
import { Row, Col, Button, Divider, Typography, Slider } from "antd";
import NoteModal from "../modal/NoteModal";
import AddedNotes from "./AddedNotes";

const { Paragraph } = Typography;

class Note extends Component {
  state = {
    rows: 1,
  };

  onChange = (rows) => {
    this.setState({ rows });
  };

  render() {
    return (
      <>
         <div id="rgt-panel" className={this.props.class}>
          <p>
            <Row>
              <Col span={24}>
                <Row className="filter-wrapper align-items-center justify-content-between px-3 pt-3">
                  <Col span="12">
                    <h6>Notes</h6>
                  </Col>
                  <Button
                    onClick={this.showModal}
                    type="link"
                    className="link pl-0 pt-0 align-items-center d-flex"
                  >
                    <span className="icon icon-close small" onClick={this.props.divClicked}/>
                  </Button>
                </Row>
                <Divider className="my-2 w-100" />
                <Col span="24" className="px-3">
                  <NoteModal />
                  <Row className="added-notes">
                    <AddedNotes />
                  </Row>
                </Col>
              </Col>
            </Row>
          </p>
        </div>
      </>
    );
  }
}

export default Note;
