import React, { Component } from "react";
import { Modal, Button, Input, Row, Form } from "antd";
import "../modal/modal.scss";

const { TextArea } = Input;
class NoteModal extends Component {
  state = { visible: false };

  showModal = () => {
    this.setState({
      visible: true,
    });
  };

  handleOk = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  handleCancel = (e) => {
    console.log(e);
    this.setState({
      visible: false,
    });
  };

  render() {
    return (
      <>
        <Button onClick={this.showModal} type="link" className="link pl-0 pt-0">
          <span className="icon icon-add-filled" />
          Take a note..
        </Button>
        <Modal
          className="note-modal"
          title="Take a Note"
          visible={this.state.visible}
          onOk={this.handleOk}
          onCancel={this.handleCancel}
          centered
          width={370}
          okButtonProps={{
            children: "Custom OK",
          }}
          cancelButtonProps={{
            children: "Custom cancel",
            className: "ant-btn-secondary",
          }}
          okText="Add"
          cancelText="Cancel"
        >
          <Row className="pt-2">
            <Form className="input-md w-100" name="validate-other">
              <Form.Item className="mb-0" name="send message">
                <TextArea
                  className="input-control"
                  placeholder="Add note here..."
                  htmlFor="send message"
                />
              </Form.Item>
            </Form>
          </Row>
        </Modal>
      </>
    );
  }
}

export default NoteModal;
