import React, { Component } from "react";
import { Typography } from "antd";

const { Paragraph } = Typography;

class AddedNotes extends Component {
  state = {
    expand: false,
    counter: 0,
  };

  typoExpand = () => {
    this.setState({
      expand: true,
      counter: !this.state.expand
        ? this.state.counter + 0
        : this.state.counter + 1,
    });
  };

  typoClose = () => {
    this.setState({
      expand: false,
      counter: !this.state.expand
        ? this.state.counter + 0
        : this.state.counter + 1,
    });
  };

  renderParagraph() {
    return (
      <div className="note-wrapper m-2" key={this.state.counter}>
        <span className="note-user">22 Apr • 04:15 • Ria Adams</span>
        <Paragraph
          ellipsis={{
            rows: 3,
            expandable: true,
            symbol: "Show more",
            onExpand: this.typoExpand,
          }}
        >
          Noted and passed on to behavioral training team for inclusion in their
          training material. Additionally, included in the examples/FAQs in
          “Info” section of the AnonyMoose app.
        </Paragraph>
      </div>
    );
  }

  render() {
    return (
      <div className="show-less">
        {this.renderParagraph()}
        {this.state.expand && (
          <a className="d-flex justify-content-end" onClick={this.typoClose}>
            Show less
          </a>
        )}
      </div>
    );
  }
}

export default AddedNotes;
