import React from "react";
import { Route, BrowserRouter, Switch } from "react-router-dom";
import ProtectedRoute from "./ProtectedRoute";
import ManageOrg from "./components/manageOrg/ManageOrg";
import ManageEmp from "./components/manageEmp/ManageEmp";
import Login from "./components/login/AmccLogin";
import OrgLogin from "./components/login/OrgccLogin";
import AmWebLogin from "./components/login/AmWebLogin";
import HotlineCreation from "./components/hotlineCreation/HotlineCreation";
import HotlineDirectory from "./components/hotlineDirectory/HotlineDirectory";
import AssignHotlineModal from "./components/modal/AssignHotlineModal";
import GridHotlineDirectory from "./components/table/TableHotlineDirectory";
import ShowAllHotlines from "./components/hotlineDirectory/ShowAllHotlines";
import HotlineMessages from "./components/hotlineMessages/HotlineMessages";

function Routes() {
  return (
    <BrowserRouter>
      <Switch>
        <Route path="/amcc" component={Login} />
        <Route path="/orgcc" component={OrgLogin} />
        <Route path="/amweb" component={AmWebLogin} />
        <ProtectedRoute
          exact={true}
          path="/hotlinedirectory"
          component={HotlineDirectory}
        />
        <ProtectedRoute path="/hotline" component={HotlineCreation} />
        <ProtectedRoute path="/organization" component={ManageOrg} />
        <ProtectedRoute exact={true} path="/employee" component={ManageEmp} />
        <ProtectedRoute path="/assignhotline" component={AssignHotlineModal} />
        <ProtectedRoute path="/displayhotlines" component={ShowAllHotlines} />
        <ProtectedRoute path="/hotlinemessages" component={HotlineMessages} />
      </Switch>
    </BrowserRouter>
  );
}

export default Routes;
