import React, { useEffect, useState } from "react";
import { Table, Input, Col, Checkbox } from "antd";

import "./table.scss";

const { Search } = Input;

const onSearch = (value) => console.log(value);

function handleChange(value) {
  console.log(`selected ${value}`);
}

const GridPermission = ({ data, deleteRecord, getAssignedValues }) => {
  const [permissionData, setPermissionaData] = useState();

  useEffect(() => {
    changeDataSource();
  }, [data]);

  const changeDataSource = () => {
    setPermissionaData(data);
  };

  const onChange = (checkedValues) => {
    console.log("check", checkedValues);
  };

  const onDelete = (key, e) => {
    e.preventDefault();
    deleteRecord(key);
  };

  const columns = [
    { title: "Name", dataIndex: "first_name", key: "first_name", width: "30%" },
    {
      title: "Respond",
      dataIndex: "can_respond",
      key: "can_respond",
      width: "20%",
      editable: true,
      render: (res) => (
        <Col span={12} className="d-flex justify-content-end">
          <Checkbox value="reports" checked={res}></Checkbox>
        </Col>
      ),
    },
    {
      title: "Reports",
      dataIndex: "can_view_reports",
      key: "can_view_reports",
      width: "20%",
      editable: true,
      render: (res) => (
        <Col span={12} className="d-flex justify-content-end">
          <Checkbox value="reports" checked={res}></Checkbox>
        </Col>
      ),
    },
    {
      title: "Action",
      dataIndex: "user_id",
      key: "user_id",
      width: "20%",
      render: (text, record) => (
        <span
          onClick={(e) => {
            onDelete(record, e);
          }}
          className="uploaded-icon icon icon-close d-inline-flex justify-content-end align-items-center"
        ></span>
      ),
    },
  ];

  return (
    <div className="no-bordered-grid">
      <Table
        className="no-bg-table"
        rowClassName={() => "editable-row"}
        columns={columns}
        dataSource={permissionData}
        rowKey={"rowkey"}
      />
    </div>
  );
};

export default GridPermission;
